// STYLE THE TAGS INSIDE THE MARKDOWN HERE
const MarkdownContent = styled.div`
  a {
    text-decoration: none;
    position: relative;
  }

  a::after {
    content: "";
    position: absolute;
    z-index: -1;
    top: 70%;
    left: -0.1px;
    right: -0.1px;
    bottom: 0;
    transition: top 0.1s ease-in-out;
    background-color: rgba(255, 250, 150, 0.8);
  }

  a:hover::after {
    top: 0;
  }
`

export default ({ data }) => {
  const post = data.markdownRemark
  const title = post.frontmatter.title;
  const subtitle = `${post.frontmatter.date} - ${post.fields.readingTime.text}`;

  return (
    <Layout siteTitle={title} siteSubtitle={subtitle}>
      <SEO
        title={title}
        description={post.frontmatter.description || post.excerpt}
      />
      <Content>
        {/* <MarkedHeader>{title}</MarkedHeader> */}
        {/* <HeaderDate>{subtitle}</HeaderDate> */}
        <MarkdownContent dangerouslySetInnerHTML={{ __html: post.html }} />
      </Content>
    </Layout>
  )
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(fields: { slug: { eq: $path } }) {
      html
      excerpt(pruneLength: 160)
      frontmatter {
        date(formatString: "DD MMMM, YYYY")
        title
      }
      fields {
        slug
        readingTime {
          text
        }
      }
    }
  }
`
